.team {
  $this: &;
  position: relative;
  width: calc(100% - 240px);
  margin: 80px auto;
  font-size: 0;
  &:not(.is-slider-init) {
    .team__arr {
      display: none; } }
  &:not(.team_md) {
    @include r(1400) {
      width: calc(100% - 180px); }
    @include xs {
      width: calc(100% - 100px); } }
  @include md {
    margin: 45px auto 0; }
  // +xs
  //   margin: 45px -20px 0
  &__in {
    height: 100%;
    margin: 0 -5px;
    z-index: 2; }
  &__item {
    display: inline-block;
    vertical-align: top;
    padding: 20px;
    text-align: center;
    // max-width: 250px
    width: calc(20% - 10px);
    white-space: normal;
    background: #fff;
    position: relative;
    margin: 0 5px 10px;
    // min-width: 190px
    &:before {
      content: '';
      position: absolute;
      background: #000;
      opacity: 0.05;
      left: 10px;
      right: 10px;
      bottom: -10px;
      height: 10px; }
    &:last-child {
      margin-right: 5px; }
    @include r(1400) {
      padding: 20px 16px; } }
  &__ava {
    max-width: 150px;
    width: 100%;
    border-radius: 50%;
    overflow: hidden;
    margin: 0 auto 40px;
    img {
      object-fit: cover; } }
  &__name {
    font-size: 20px;
    padding-bottom: 40px;
    margin: 0 auto 40px;
    border-bottom: 2px solid $green;
    @include lora-bi; }
  &__desc {
    font-size: 14px;
    margin-bottom: 40px;
    @include ubuntu-l; }
  &__arr {
    position: absolute;
    top: 258px;
    // margin-top: -4px
    width: 120px;
    height: 80px;
    background: $orange;
    z-index: 1;
    @include tr(background .2s);
    &:hover {
      background: $dark-orange; }
    @include r(1400) {
      width: 285px; }
    @include xs {
      width: 100px; }
    .icon {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      fill: #fff;
      font-size: 10px; } }
  &__arr-prev {
    right: calc(100% - 5px);
    @include r(1400) {
      right: auto;
      left: -85px; }
    @include xs {
      left: -70px; }
    .icon {
      left: 30px;
      @include xs {
        left: 20px; } } }
  &__arr-next {
    left: calc(100% + 5px);
    @include r(1400) {
      left: auto;
      right: -95px; }
    @include xs {
      right: -70px; }
    .icon {
      right: 30px;
      @include xs {
        right: 20px; } } }
  &__left {
    float: left;
    width: 150px;
    margin-right: 80px;
    @include md {
      margin-right: 40px; }
    @include sm {
      float: none;
      width: 100%;
      margin-right: 0;
      margin-bottom: 40px; } }
  &__right {
    overflow: hidden;
    text-align: left; }
  &__wrap {
    border-bottom: 2px solid $green;
    padding-bottom: 20px;
    margin-bottom: 40px; }
  &_md {
    width: 100%;
    margin: 0;
    @include md {
      margin: 0; }
    #{$this}__in {
      margin: 0; }
    #{$this}__ava {
      margin-bottom: 0;
      @include sm {
        max-width: 75px; } }
    #{$this}__name {
      font-size: 36px;
      margin: 0 0 40px 0;
      padding: 0 30px 0 0;
      border: none;
      text-align: left;
      @include lora-bi;
      @include md {
        margin: 0 0 20px 0;
        font-size: 26px; } }
    #{$this}__desc {
      font-size: 18px;
      margin: 0;
      @include ubuntu-l;
      @include md {
        font-size: 14px; }
      > p {
        &:not(:last-child) {
          margin-bottom: 20px; } } }
    #{$this}__item {
      padding: 0;
      width: 100%;
      margin: 0;
      background: transparent;
      @include clr;
      &:before {
        display: none; } }
    #{$this}__wrap {
      padding-bottom: 10px;
      margin-bottom: 20px; } }


  .slick-slide {
    margin: 0 5px; } }
