.accordion {
  position: relative;
  background: #fff;
  margin: -80px 0 80px;
  @include md {
    margin: -50px 0 50px; }
  &:before {
    content: '';
    position: absolute;
    background: #000;
    opacity: 0.05;
    left: 10px;
    right: 0;
    bottom: -10px;
    height: 10px; }
  &__item {
    position: relative;
    padding: 0 40px;
    @include md {
      padding: 0 30px; }
    &:not(:last-child) {
      border-bottom: 4px solid rgba(#000, .05); }
    &:after {
      content: '';
      position: absolute;
      background: #000;
      opacity: 0.05;
      right: -10px;
      top: 10px;
      bottom: -10px;
      width: 10px; } }
  &__header {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: 37px 0px;
    @include md {
      padding: 15px 0 18px; }
    &.is-active {
      .icon-in-circle {
        @include rotate(180); }
      .icon-in-circle__in {
        span:last-child {
          opacity: 0; } } } }
  &__col {
    @include md {
      text-align: left; }
    &:first-child {
      @include md {
        display: none; } }
    .h5 {
      @include md {
        line-height: 1.5; } } }
  &__content {
    display: none;
    border-top: 2px solid $green;
    padding: 0 0px; }
  &__text {
    font-size: 12px;
    text-transform: uppercase;
    @include ubuntu-l;
    > span {
      padding: 0 5px; } }

  &__desc {
    font-size: 16px;
    @include ubuntu-l;
    @include sm {
      font-size: 12px; } }
  &__list {
    li:not(:last-child) {
      margin-bottom: 13px; } }
  &__line {
    display: flex;
    flex-flow: row nowrap;
    align-items: flex-start;
    justify-content: flex-start;
    padding: 40px 0;
    &:not(:last-child) {
      border-bottom: 2px solid $light-gray; }
    &:last-child {
      padding-bottom: 80px;
      @include md {
        padding-bottom: 50px; } }
    @include md {
      padding: 25px 0 30px;
      flex-direction: column; } }
  &__time {
    font-size: 18px;
    flex-basis: 160px;
    max-width: 160px;
    margin-top: 3px;
    @include lora;
    @include md {
      flex-basis: 100%;
      max-width: 100%;
      margin: 0 0 18px;
      font-size: 14px; } }
  &__about {
    flex: 1 0;
    padding-right: 30px;
    @include md {
      padding-right: 0; }
    .desc {
      margin-bottom: 30px;
      &:only-child {
        margin-bottom: 0; }
      @include md {
        margin-bottom: 20px; } }
    .title-sm {
      @include rmin(980) {
        display: none; } }
    & + .accordion__additionally {
 } }      // margin-top: 20px

  &__additionally {
    flex-basis: 320px;
    max-width: 320px;
    @include r(1400) {
      flex-basis: 230px;
      max-width: 230px; }
    @include md {
      flex-basis: 100%;
      max-width: 100%; }
    .title-sm {
      max-width: 280px;
      @include md {
        max-width: 100%; } } }
  &__line-diff {
    .accordion__about {
      .title-sm {
        @include md {
          margin-bottom: 19px;
          font-size: 14px; } } }
    .accordion__additionally {
      .title-sm {
        @include md {
          display: none; } } } } }

.icon-in-circle {
  position: relative;
  display: inline-block;
  width: 54px;
  height: 54px;
  border-radius: 50%;
  border: 2px solid transparent;
  background: $orange;
  @include tr(transform .2s ease-in-out);
  &:hover {
    background: transparent;
    border-color: $orange;
    span {
      background: $orange; } }
  @include md {
    width: 28px;
    height: 28px;
    margin-top: 5px; }
  &__in {
    @include vcenter;
    > span {
      background: #fff;
      @include vcenter;
      @include tr(opacity .15s ease-in);
      &:first-child {
        width: 18px;
        height: 2px;
        @include md {
          width: 10px; } }
      &:last-child {
        width: 2px;
        height: 18px;
        margin-top: -9px;
        @include rotate(180);
        @include md {
          height: 10px;
          margin-top: -5px;
          margin-left: -1px; } } } } }


.link {
  color: $green;
  font-size: 16px;
  @include ubuntu-l;
  @include tr(opacity .15s ease-in);
  &:hover {
    opacity: .8;
    .icon {
      transform: translateX(5px); } }
  @include md {
    font-size: 14px; }
  .icon {
    font-size: 10px;
    margin-left: 10px;
    fill: $green;
    @include tr(transform .15s ease-in);
    @include md {
      margin-left: 5px; } } }

.member {
  max-width: 180px;
  width: 100%;
  @include clr;
  @include md {
    max-width: 100%; }

  &__pic {
    display: inline-block;
    width: 72px;
    height: 72px;
    border-radius: 50%;
    overflow: hidden;
    margin-bottom: 15px;
    @include md {
      display: block;
      margin-bottom: 0;
      float: left;
      margin-right: 30px; }
    @include xs {
      display: inline-block;
      float: none; } }
  &__name {
    display: inline-block;
    font-size: 16px;
    width: 100%;
    @include ubuntu-b;
    @include md {
      width: calc(100% - 87px - 15px);
      margin-top: 20px;
      font-size: 14px;
      &:after {
        content: '\A';
        white-space: pre; } }
    @include xs {
      width: 100%;
      margin-top: 0;
      &:after {
        display: none; } } }
  &__desc {
    font-size: 14px;
    color: $black;
    @include ubuntu-l; } }
