.input {
  input {
    width: 100%;
    border: 2px solid rgba(#fff, .35);
    background: transparent;
    font-size: 12px;
    padding: 19px 20px;
    color: #fff;
    text-transform: uppercase;
    @include tr(border-color .2s ease-in-out);
    @include ubuntu;
    &:hover {
      border-color: $orange; }
    &:focus {
      @include placeholder {
        opacity: 0; } }
    @include placeholder {
      font-size: 12px;
      color: #fff;
      letter-spacing: 1.3px;
      @include tr(opacity .2s);
      @include ubuntu-l; } } }


.checkbox {
  padding: 4px 0;

  input {
    position: absolute;
    z-index: -1;
    opacity: 0;
    &:checked + label:before {
      border-color: #fff; }
    &:checked + label:after {
      opacity: 1; }
    &:hover {
      & + label {
        &:before {
          border-color: #fff; } } } }
  label {
    position: relative;
    padding: 4px 0 0 45px;
    cursor: pointer;
    font-size: 14px;
    color: #fff;
    @include ubuntu-l;
    @include xs {
      display: block;
      padding: 0px 0 0 40px; }
    &:before {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      width: 24px;
      height: 24px;
      border: 2px solid rgba(#fff, .35);
      background: trasparent;
      transition: border-color .2s ease-in-out, opacity .2s ease-in-out;
      @include xs {
        top: 4px; } }
    &:after {
      content: '';
      position: absolute;
      opacity: 0;
      top: 8px;
      left: 8px;
      width: 8px;
      height: 8px;
      background: #fff;
      @include tr(border-color .2s ease-in-out);
      @include xs {
        top: 12px; } }
    > a {
      color: $green;
      text-decoration: underline;
      &:hover {
        text-decoration: none; }
      @include xs {
        display: block; } } } }
