.download {
  display: block;
  max-width: 380px;
  margin: 0 auto 80px;
  @include md {
    max-width: 280px;
    margin-bottom: 50px; }
  &__in {
    margin-bottom: 35px;
    @include clr;
    @include md {
      margin-bottom: 20px; } }
  &__pic {
    float: left;
    margin-right: 40px;
    @include md {
      margin-right: 30px; }
    img {
      @include md {
        width: 51px; } } }
  &__text {
    overflow: hidden;
    line-height: 1.2;
    @include md {
      font-size: 16px; } }

  .btn {
    font-size: 22px; } }
