$aboutMargin: 31px;

.about-tour {
  $this: &;
  font-size: 0;
  margin: 120px 0 50px;
  @include md {
    margin: 75px 0 30px; }
  // overflow: hidden
  &__item {
    position: relative;
    display: inline-block;
    vertical-align: top;
    width: 33.3%;
    @include xs {
      width: 100%;
      &:not(:last-child) {
        margin-bottom: 40px; } }
    &:nth-child(3n+1) {
      #{$this}__content {
        margin: -40px 0px 0 165px;
        z-index: 4;
        @include r(1800) {
          margin: -30px 0 0 40px; }
        @include md {
          margin: -35px 0 0 15px; } } }
    &:nth-child(2n+2) {
      margin-top: -40px;
      @include md {
        margin-top: -20px; }
      #{$this}__content {
        z-index: 3;
        margin: 5px 0 0 -20px;
        @include r(1800) {
          margin: -79px 0 0 -60px; }
        @include md {
          margin: -35px 0 0 -15px; } }
      #{$this}__img {
        img {
          object-fit: cover;
          object-position: top;
          max-height: 360px; } } } }
  &__img {
    margin: 0 auto;
    @include xs {
      display: inline-block;
      width: 270px; }
    img {
      object-fit: contain;
      width: 100%;
      @include md {
        height: 252px;
        object-fit: cover;
        object-position: 30%; }
 } }      // width: 100%
  &__content {
    // position: absolute
    // bottom: 0
    text-align: center;
    background: #fff;
    padding: 40px 25px;
    max-width: 540px;
    width: 100%;
    @include border;
    @include r(1400) {
      min-width: 415px; }
    @include lg {
      min-width: 300px;
      padding: 20px 10px; }
    @include md {
      min-width: 210px; }
    @include xs {
      min-width: auto;
      max-width: 270px; }
    > svg {
      fill: $orange;
      width: 190px;
      height: 8px;
      @include md {
        width: 96px; } } }
  &__desc {
    font-size: 20px;
    padding: 37px 50px;
    @include lora-i;
    @include r(1400) {
      padding: 37px 60px; }
    @include md {
      font-size: 16px;
      padding: 12px 10px 20px; } }


  &__item_men,
  &__item_pizza {
    #{$this}__img {
 } }      // max-width: 600px

  &__item_men {
    #{$this}__content {
 } }      // right: -110px


  &__item_pizza {
    #{$this}__content {
      margin: -32px 0 0 -120px;
      @include r(1800) {
        margin: -55px 0 0 -40px; }
      @include md {
        margin: -35px 0 0 -15px; }
      @include xs {
        margin: -35px 0 0 30px; }
      // left: -70px
 } }      // bottom: -20px

  &__item_chief {
    padding-left: 62px;
    padding-right: 62px;
    @include md {
      padding-left: 20px;
      padding-right: 20px; }
    @include xs {
      padding: 0;
      text-align: right; }
    #{$this}__img {
      max-width: 500px;
      margin: 0 auto;
      text-align: center; }

    #{$this}__content {} }
  //     right: 40px
  //     bottom: -50px
  // // &__item_chief
 }  // &__item_pizza
