.style-list {
  margin: 40px 0;
  font-size: 0;
  @include md {
    margin: 25px 0; }
  &__item {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    font-size: 16px;
    line-height: 1.5;
    @include ubuntu-l;
    @include md {
      font-size: 14px;
      line-height: 1.7; }
    &:not(:last-child) {
      padding-right: 24px;
      margin-right: 22px;
      &:after {
        content: '';
        position: absolute;
        right: 0;
        top: 6px;
        width: 8px;
        height: 8px;
        border-radius: 50%;
        background: $green; } } }
  &_width {
    .style-list__item {
      display: block;
      font-size: 18px;
      padding-left: 32px;
      margin-bottom: 13px;
      line-height: 1.3;
      &:not(:last-child) {
        @include xs {
          padding-right: 0;
          margin-right: 0; } }
      &:after {
        display: none; }
      &:before {
        content: '';
        position: absolute;
        right: 0;
        top: 6px;
        right: auto;
        left: 0;
        width: 9px;
        height: 9px;
        border-radius: 50%;
        background: $orange; }

      @include md {
        font-size: 14px;
        margin-bottom: 16px; } } } }



.image-list {
  font-size: 0;
  max-width: 82%;
  margin: 0 auto 80px;
  @include r(1400) {
    max-width: 95%; }
  @include md {
    margin: 0 auto 10px;
    // max-width: 100%
    text-align: center; }
  &__item {
    display: inline-block;
    vertical-align: top;
    width: 20%;
    text-align: center;
    padding: 0 30px;
    @include r(1400) {
      padding: 0 15px; }
    @include md {
      width: 33.3%;
      margin-bottom: 40px;
      padding: 0 13px; }
    @include xs {
      width: 50%; }
    > img {
      max-width: 150px;
      margin-bottom: 35px;
      @include md {
        max-width: 80px;
        margin-bottom: 15px; } }
    // .desc-sm
    //   max-width: 175px
 } }    //   margin: 0 auto
