.form {
  max-width: 750px;
  width: 100%;
  padding: 80px 40px 80px 130px;
  @include md {
    margin: 0 auto;
    padding: 50px 60px; }
  @include xs {
    padding: 50px 20px; }
  &__title {
    color: $orange;
    margin-bottom: 40px;
    @include md {
      text-align: center;
      margin-bottom: 28px; } }
  &__line {
    margin-bottom: 20px; }
  &__btn {
    margin-top: 40px;
    @include md {
      margin-top: 25px; } } }


.form-details {
  padding: 80px 0;
  @include md {
    padding: 40px 0 45px;
    text-align: center; }
  &__title {
    margin-bottom: 80px;
    @include md {
      font-size: 36px;
      margin-bottom: 45px; } }
  &__text {
    font-size: 16px;
    margin-bottom: 70px;
    @include ubuntu-l;
    @include md {
      font-size: 14px;
      margin-bottom: 38px; } }
  &__phone {
    display: block;
    color: $black;
    font-size: 24px;
    @include lora-b;
    @include tr(opacity .2s);
    &:hover {
      opacity: 0.8; } }
  &__email {
    display: block;
    font-size: 16px;
    text-decoration: underline;
    color: $green;
    margin-top: 30px;
    @include ubuntu-l;
    &:hover {
      text-decoration: none; } } }
