.places {
  $this: &;
  &__in {
    position: relative;
    border-radius: 50%;
    margin: 0 auto 40px;
    box-sizing: border-box;
    @include md {
      margin-bottom: 30px; }
    &:before {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      z-index: 1;
      border-radius: 50%; }
    #{$this}__center {
      @include vcenter; } }

  &__circle {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 9;
    transform: rotateY(-180deg) rotateZ(-90deg);
    circle {
      fill: none; } }
  &__title-in {
    font-size: 20px;
    margin-bottom: 10px;
    @include lora-i;
    @include md {
      font-size: 14px; } }
  &__center {
    .h4 {
      @include md {
        font-size: 18px;
        margin-top: -10px; } } }

  &_white {
    color: #fff;
    #{$this}__title {
      font-size: 20px;
      margin-bottom: 20px;
      letter-spacing: 1.2px;
      @include ubuntu-l;
      @include md {
        font-size: 16px; } }
    #{$this}__in {
      width: 140px;
      height: 140px;
      &:before {
        border: 10px solid $dark-orange; } }
    #{$this}__circle {
      width: 140px;
      height: 140px;
      circle {
        stroke-dasharray: 790px;
        stroke-dashoffset: -440px;
        stroke-width: 10px;
        stroke: white;
        animation: countdown 5s linear infinite forwards; } } }

  &_green {
    color: $black;
    #{$this}__title {
      font-size: 20px;
      margin-bottom: 20px;
      @include ubuntu-l; }

    #{$this}__in {
      width: 380px;
      height: 380px;
      @include md {
        width: 220px;
        height: 220px; }
      &:before {
        border: 10px solid rgba($green, .25); }
      span {
        font-size: 72px;
        @include lora-b;
        @include md {
          font-size: 48px;
          line-height: 1; } } }
    #{$this}__circle {
      width: 380px;
      height: 380px;
      @include md {
        width: 220px;
        height: 220px; }
      circle {
        stroke-dasharray: 790px;
        stroke-dashoffset: -440px;
        stroke: white;
        stroke-width: 4px;
        animation: countdown 5s linear infinite forwards;
        @include md {
          stroke-width: 7px;
          r: 73.5; } } }
    .icon-decor-line-top {
      fill: $green;
      max-width: 192px;
      width: 100%;
      @include md {
        max-width: 96px; } } }

  & + .btn {
    @include md {
      font-size: 22px; } } }
@keyframes countdown {
  from {
    stroke-dashoffset: -440px; }
  to {
    stroke-dashoffset: 0px; } }
