.cost {
  $this: &;
  font-size: 0;
  margin: 0 -15px 80px;
  @include clr;
  @include md {
    margin: 50px 0; }
  @include xs {
    margin: 50px 0 80px; }
  & + .section__decor {
    @include xs {
      display: none; } }
  &__item {
    display: inline-block;
    vertical-align: top;
    width: calc(33.3% - 30px);
    margin: 0 15px;
    text-align: center;
    padding: 40px 0;
    @include md {
      width: calc(100% - 390px);
      margin: 0; }
    @include xs {
      width: 100%; }
    &:nth-child(2) {
      .icon-decor-line-bottom {
        @include md {
          display: none; } }
      .cost__decor-in {
        @include md {
          position: relative;
          &:after {
            content: '';
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
            width: 96px;
            height: 1px;
            background: $orange; } } } }
    &:nth-child(3) {
      .icon-decor-line-top {
        @include md {
          display: none; } } }


    &.is-active {
      background: $orange;
      color: #fff;
      @include border;
      @include md {
        float: left;
        max-width: 380px;
        width: 100%;
        padding: 30px 0 40px; }
      @include xs {
        margin-bottom: 40px; }
      #{$this}__btn {
        pointer-events: auto;
        background: #fff;
        color: $orange;
        &:hover {
          color: #fff;
          background: $dark-orange; } }
      #{$this}__decor {
        @include md {
          margin: 10px 0 15px; }
        > .icon {
          fill: #fff; } }
      #{$this}__decor-in {
        @include md {
          padding: 20px 0 5px; } } }

    &:not(.is-active) {
      @include md {
        padding: 0 0 0 30px; }
      @include xs {
        padding: 0; }
      #{$this}__btn {
        @include md {
          display: none; } }
      #{$this}__decor {
        @include md {
          margin: 20px 0 -20px 0; } }
      #{$this}__decor-in {
        @include md {
          padding: 20px 0 2px; } } } }


  &__btn {
    background: $gray;
    color: #fff;
    pointer-events: none;
    padding-left: 35px;
    padding-right: 35px; }

  &__decor {
    margin: 0 0 40px 0;
    &:not(.is-active) {
      @include md; }

    > .icon {
      max-width: 192px;
      width: 100%;
      height: 8px;
      fill: $orange;
      @include md {
        max-width: 96px;
        height: 4px; } } }
  &__decor-in {
    padding: 35px 0 25px; }
  .h3 {
    line-height: 1.4;
    @include md {
      font-size: 36px;
      line-height: 1.8; } } }


.cost-additional {
  font-size: 0;
  margin: 75px auto 35px;
  @include md {
    margin: 45px 0 0 0; }
  &__col {
    display: inline-block;
    vertical-align: top;
    width: 50%;
    padding-right: 8%;
    padding-left: 110px;
    @include r(1400) {
      padding-right: 0%;
      padding-left: 85px; }
    @include md {
      width: 100%;
      display: block;
      padding-left: 0;
      margin-bottom: 40px; } }
  &__title {
    font-size: 30px;
    @include lora-i;
    @include md {
      text-align: center;
      font-size: 24px; } } }
