.container {
  position: relative;
  max-width: 1570px;
  width: 100%;
  padding: 0 20px;
  margin: 0 auto;
  @include clr;
  @include r(1400) {
    max-width: 1280px;
    padding: 0 45px; }

  @include r(1200) {
    padding: 0 30px; }
  @include xs {
    padding: 0 20px; }
  &__md {
    @include r(1400) {
      max-width: 80%;
      margin: 0 auto; }
    @include md {
      max-width: 100%; } } }


.l {
  display: flex;
  flex-flow: row nowrap;
  &:not(.l_not-col) {
    @include xs {
      flex-direction: column; } }
  &_stretch {
    align-items: stretch;
    height: 100%; }
  &_center {
    align-items: center; }
  &_content-right {
    justify-content: flex-end; }
  &_content-left {
    justify-content: flex-start; }
  &_between {
    justify-content: space-between; }
  &_around {
    justify-content: space-around; }
  &_half {
    .l__col {
      max-width: 50%;
      flex-basis: 50%;
      @include xs {
        max-width: 100%;
        flex-basis: 100%; } } }
  &_not-half {
    .l__col {
      &:first-child {
        max-width: 45%;
        flex-basis: 45%; }
      &:last-child {
        max-width: 55%;
        flex-basis: 55%; } } }
  &_col-md {
    @include md {
      flex-direction: column; }
    &.l_not-half {
      .l__col {
        @include md {
          overflow: hidden;
          flex-basis: 100%;
          max-width: 100%; } } } }
  &__col {
    flex: 1 0 auto; }

  &__col-order-1-xs {
    @include xs {
      order: 1; } }
  &__col-order-2-xs {
    @include xs {
      order: 2; } }
  &__col-order-3-xs {
    @include xs {
      order: 3; } } }
