.organization {
  padding: 80px 0 60px;
  @include md {
    padding: 45px 0 30px; }
  &__title {
    @include md {
      margin-bottom: 50px; } }
  &__text {
    columns: 2;
    column-gap: 40px;
    font-size: 18px;
    margin-bottom: 80px;
    line-height: 1.35;
    @include ubuntu-l;
    @include md {
      columns: 1;
      column-gap: normal;
      font-size: 14px;
      margin-bottom: 40px; }
    > p {
      overflow: hidden;
      break-inside: avoid-column;
      -webkit-column-break-inside: avoid;
      &:not(:last-child) {
        @include md {
          margin-bottom: 20px; } } } }

  .icon-decor-line-top,
  .icon-decor-line-bottom {
    max-width: 192px;
    margin-bottom: 30px;
    fill: $green;
    @include md {
      max-width: 96px;
      margin-bottom: 10px; } } }
